export default {
	data() {
		return {
			yinyeShow: true,
			yinyeShow2: true,
			yinyeShow3: true,
			yinyeShow4: true,
			imgUrl: "",
			sfzzmImgUrl: "",
			sfzfmImgUrl: "",
			scImgUrl: "",
			maskShow: false
		}
	},
	methods: {
		uploadPic(v) {
			var that = this;
			if(v==='1'){
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: [], //album 从相册选图，camera 使用相机，默认二者都有
					success: function (res) {
						that.imgUrl = res.tempFilePaths[0];
						that.yinyeShow = false;    
					},
					
				});
			}else if(v==='2'){
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: [], //album 从相册选图，camera 使用相机，默认二者都有
					success: function (res) {
						that.sfzzmImgUrl = res.tempFilePaths[0];
						that.yinyeShow2 = false;    
					},
					
				});
			}else if(v==='3'){
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: [], //album 从相册选图，camera 使用相机，默认二者都有
					success: function (res) {
						that.sfzfmImgUrl = res.tempFilePaths[0];
						that.yinyeShow3 = false;    
					},
					
				});
			}else if(v==='4'){
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: [], //album 从相册选图，camera 使用相机，默认二者都有
					success: function (res) {
						that.scImgUrl = res.tempFilePaths[0];
						that.yinyeShow4 = false;    
					},
					
				});
			}
			
		},
		togglePopup(open) {
			if (open === 'tip') {
				this.maskShow = true
			} 
		},
		cancel(v) {
			this.maskShow = false
			if (v === '1') {
				 uni.switchTab({
					url: "/pages/home/home"
				})
			} 
					 
		}
	}
	
}
